@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (max-width: 576px) {
  html {
    font-size: 14px;
  }
}
.App {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(to top, #a8ff78, #78ffd6);
}

.App__wrapper {
  max-width: 700px;
  margin: 0 auto;
  padding: 0 1.5625rem 1.5625rem;
}

.App.hot {
  background: linear-gradient(to top, #fbd400, #f97c00);
}

.App.rain {
  background: linear-gradient(to top, #bdc3c7, #2c3e50);
}

.App.cold {
  background: linear-gradient(to top, #1CB5E0, #000046);
}
.search {
  width: 100%;
  margin: 0 0 75px;
}

@media (max-width: 576px) {
  .search {
    margin: 0 0 55px;
  }
}

.search__bar {
  color: black;
  font-size: 15px;
  display: block;
  width: 100%;
  padding: 0.9375rem;
  border: none;
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 15px;
  box-shadow: 0px 2px rgba(129, 123, 123, 0.3);
  background: rgba(255, 255, 255, 0.52);
  transition: 0.4s ease;
}

.search .search__bar:focus {
  background-color: white;
}
.spinner {
  width: 100%;
}

.spinner__image {
  display: block;
  margin: 0 auto;
}
.location {
  color: white;
  font-size: 2.1875rem;
  text-align: center;
  text-shadow: 2px 2px rgba(58, 53, 53, 0.73);
  cursor: default;
}
.weather {
  text-align: center;
  color: white;
  font-weight: 700;
  text-shadow: 2px 2px rgba(58, 53, 53, 0.73);
  cursor: default;
}

.weather__temperature {
  position: relative;
  display: inline-block;
  margin: 1.875rem auto;
  padding: 0.9375rem 1.5625rem;
  font-size: 6.25rem;
  background-color: rgb(255, 255, 255, 0.1);
  border-radius: 16px;
  box-shadow: 0.1875rem 0.1875rem rgba(58, 53, 53, 0.73);
}

.weather__description {
  font-size: 3.125rem;
}

@media (max-width: 576px) {
  .weather__description {
    font-size: 2.1rem;
  }
}
.header {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  width: 100%;
  height: 60px;
  background-color: rgba(255, 255, 255, .5);
  box-shadow: 0px 2px rgba(129, 123, 123, 0.3);
}

.header__container {
  display: flex;
  justify-content: space-between;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 25px;
}

.header__logo {
  font-family: Roboto;
  font-size: 2rem;
  text-shadow: 2px 2px rgba(58, 53, 53, 0.73);
  color: white;
  cursor: default;
}

.header__toggle {
  width: 44px;
  height: 44px;
  font-family: Roboto;
  color: white;
  font-size: 1.5rem;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 2px 2px rgba(58, 53, 53, 0.73);
  border: 1px solid rgb(185, 184, 184);
  border-radius: 7px;
  background-color: transparent;
  box-shadow: 0 0 2px rgba(129, 123, 123, 0.3);
  cursor: pointer;
}
